<template>
  <div>
    <v-tabs
      class="tabs"
      centered
      grow
      height="60px"
      v-model="activeTab"
    >
      <v-tab v-for="tab in tabs" :key="tab.id" :to="tab.route" exact  :class="{ 'is-active': tab.isActive }">
        {{ tab.name }}
      </v-tab>
    </v-tabs>
    <router-view></router-view>
</div>
</template>
<script>
export default {
  data() {
    return {
      tabs: [
        { id: 1, name: "Project", route: '/project-description', isActive: true },
        { id: 2, name: "To do", route: `/done`, isActive: false},
        { id: 3, name: "Doing", route: '/done', isActive: false },
        { id: 4, name: "Done", route: '/done', isActive: false}
      ]
    };
  }
}
</script>